import React from 'react';

class TabComp extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      findSelected: '',
    };
  }

  onHandleChange = (index) => {
    this.setState({ findSelected: index });
  }

  handleKeyPress = () => {

  }

  render() {
    const { selected, children } = this.props;
    const { findSelected } = this.state;
    return (
      <div>
        <ul className="inline">
          {children.map((elem, index) => {
            const style = index === selected ? 'selected' : '';
            return (
              <li className={style} key={elem.props.uniqueKey}>                
                <div
                  role="button"
                  onClick={this.onHandleChange(this, index)}
                  onKeyPress={() => {}}
                  tabIndex={0}
                >
                  {elem.props.title}
                </div>
              </li>
            );
          })}
        </ul>
        <div className="tab">{children[findSelected]}</div>
      </div>
    );
  }
}

export default TabComp;
